import logoPHP from './img/skill/php.png';
import logoSQL from './img/skill/mysql.png';
import logoHTML from './img/skill/HTML.png';
import logoCSS from './img/skill/CSS.png';
import logoReact from './img/skill/react.png';
import logoPython from './img/skill/python.png';
import logoR from './img/skill/r.png';
import logoCodeigniter from './img/skill/codeigniter.png';
import logoLaravel from './img/skill/laravel.png';
import logoJS from './img/skill/JS.png';
import logoBootstrap from './img/skill/bootstrap.png';
import logoZurb from './img/skill/zurbfoundation.png';

const Skill = () => {
    const skill = {
        'intermediate':[
            {'id':1, 'url':logoPHP, 'name':'Logo PHP'},
            {'id':2, 'url':logoSQL, 'name':'Logo MySQL'},
            {'id':3, 'url':logoHTML, 'name':'Logo HTML'},
            {'id':4, 'url':logoCSS, 'name':'Logo CSS'},
            {'id':5, 'url':logoCodeigniter, 'name':'Logo Codeigniter'},
            {'id':6, 'url':logoZurb, 'name':'Logo Zurb Foundation'}
        ], 

        'beginner':[
            {'id':1, 'url':logoBootstrap, 'name':'Logo Bootstrap'},
            {'id':2, 'url':logoLaravel, 'name':'Logo Laravel'},
            {'id':3, 'url':logoJS, 'name':'Logo JS'},
            {'id':4, 'url':logoReact, 'name':'Logo React'},
            {'id':5, 'url':logoPython, 'name':'Logo Python'},
            {'id':6, 'url':logoR, 'name':'Logo R'}
        ]
    }
    return(
        <div id="skill" className="py-5 bg-light">
            <div className="container py-5 mb-5">
                <div className="row">
                    <div className="col">
                        <div className="text-title">Intermediate</div>
                        <div className="skill-detail">
                            <ul className="d-none d-sm-flex list-group list-group-horizontal-sm justify-content-center">
                                {skill['intermediate'].map((skill) => (
                                    <li key={skill.id} className="list-group-item">
                                        <img src={skill.url} alt={skill.name}></img>
                                    </li>
                                ))}
                            </ul>
                            <div className="d-sm-none row mt-4">
                                {skill['intermediate'].map((skill) => (
                                    <div key={skill.id} className="col-4 mb-4">
                                        <img src={skill.url} alt={skill.name}></img>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-5">
                    <div className="col">
                        <div className="text-title">Beginner</div>
                        <div className="skill-detail">
                            <ul className="d-none d-sm-flex list-group list-group-horizontal-sm justify-content-center">
                                {skill['beginner'].map((skill) => (
                                    <li key={skill.id} className="list-group-item">
                                        <img src={skill.url} alt={skill.name}></img>
                                    </li>
                                ))}
                            </ul>
                            <div className="d-sm-none row mt-4">
                                {skill['beginner'].map((skill) => (
                                    <div key={skill.id} className="col-4 mb-4">
                                        <img src={skill.url} alt={skill.name}></img>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Skill;